@import '../../styles/styles.scss';

.ChatChoice{
    background-color: $light;
    position: fixed;
    width: 100vw;
    height: 100%;
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    form{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items:start;
        label{
            width: 20vw;
            font-size: 1.5rem;
            margin-top: 10px;
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-gap: 10px;
            align-items: center;
            justify-items: start;
            input{
                justify-self: end;
                
            }
            textarea{
                justify-self: end;
                height: 20vh;
            }
            select{
                justify-self: end;
                width:100%;
            }
            option{
                justify-self: end;
            }
        }
        .SubmitButton{
            width: 100%;
            background-color: white;
            margin-top: 10px;
            border-radius: 2px;
            align-self: center;
            user-select: none;
            cursor: pointer;
            transition: all 0.3s;
            &:hover{
                background-color: $primary-color;
                filter:drop-shadow(0px 0px 5px black);
                color: black;
                transform: scale(0.9);
            }
        }
        
    }
}

.BackImage{
    position: fixed;
    width: 100vw;
    height: 100vh;
    z-index: -1;
    background-image: url('https://images.unsplash.com/photo-1612833946780-3e3e3e3e3e3e');
    background-size: cover;
    background-position: center;
    filter: blur(5px);
    opacity: 0.5;
    z-index: -1;
    user-select: none;
}
.Form{
    padding: 1rem;
    background: rgba(37, 20, 20, 0.39);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(14.7px);
    -webkit-backdrop-filter: blur(14.7px);
    border: 1px solid rgba(37, 20, 20, 0.3);
    
}